/* Mobile landscape and portrait displays less than 568px width*/
@import url("https://fonts.googleapis.com/css2?family=Righteous&display=swap");


/* container div, which contains image, title and description of the service*/
.container-div {
  position: relative;
  margin: 3vw;
  text-align: center;
  background-repeat: repeat;
  cursor: pointer;
}

/* container div which forces image to fit in it */
.container-image-serv {
  background-size: contain;
  overflow: hidden;
  cursor: pointer;
}


/* image's class for a smooth transition during zoom */
.image-serv {
  width: 100%;
  display: block;
  transition: transform 0.5s ease;
  -webkit-transition: transform 0.5s ease;
  -moz-transition: transform 0.5s ease;
  -ms-transition: transform 0.5s ease;
  -o-transition: transform 0.5s ease;
}

/* class for the zoom effect which takes place during the image's hover above div.container-image-serv*/
.container-image-serv:hover img {
  cursor: pointer;
  transform: scale(1.1);
  -webkit-transform: scale(1.1);
  -moz-transform: scale(1.1);
  -ms-transform: scale(1.1);
  -o-transform: scale(1.1);
}

.container-image-serv:active img {
  cursor: pointer;
  filter: saturate(10);
  transform: scale(1.1);
  -webkit-transform: scale(1.1);
  -moz-transform: scale(1.1);
  -ms-transform: scale(1.1);
  -o-transform: scale(1.1);
  -webkit-filter: saturate(10);
}


/* container for the position of service's title*/
.services-title-container {
  cursor: pointer;
  position: absolute;
  text-decoration: none;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  -webkit-transform: translate(-50%, -50%);
  -moz-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  -o-transform: translate(-50%, -50%);
  transition: 0.5s;
  -webkit-transition: 0.5s;
  -moz-transition: 0.5s;
  -ms-transition: 0.5s;
  -o-transition: 0.5s;
}

/* class for service's title and strikethrough "animation" */
.services-title {
  cursor: pointer;
  font-family: "Righteous", cursive;
  font-size: 22px;
  color: #70cdc3;
  width: 100%;
  background-image: linear-gradient(to bottom,
      rgba(112, 205, 195, 0) 50%,
      rgba(112, 205, 195, 1) 50%,
      rgba(112, 205, 195, 1) 60%,
      rgba(112, 205, 195, 0) 60%);
  background-size: 0 100%;
  background-repeat: no-repeat;
  transition: 250ms linear;
}

/* title's hover effect which extends its size*/
span.services-title:hover {
  background-size: 100% 100%;
}


/* container for the service's description */
.services-description-container {
  cursor: pointer;
  visibility: hidden;
  background-color: rgba(0, 0, 0, 0);
  transition: 0.5s ease;
  position: absolute;
  text-align: center;
  bottom: 0;
  left: 50%;
  width: 100%;
  padding: 5px 10px;
  transform: translate(-50%);
  -webkit-transform: translate(-50%);
  -moz-transform: translate(-50%);
  -ms-transform: translate(-50%);
  -o-transform: translate(-50%);
}

/* class for the properties of text description */
.services-description {
  cursor: pointer;
  font-family: "Montserrat", "Roboto", sans-serif;
  font-size: 12px;
  line-height: 1.3;
  color: #70cdc3;
  text-align: center;
}

/* removal of p's padding-bottom space. This space was inherited from span */
span.services-description p {
  padding: 0;
  margin: 0;
  padding-bottom: 0;
}

/*property for the visibility of grey box when service's description is hovered*/
div.container-div:hover div.services-description-container {
  visibility: visible;
}

/* property for the amount of grey box's visibility*/
div.services-description-container:hover {
  background-color: rgba(0, 0, 0, 0.5);
}




/* 80s background pattern for the entire page */
.pattern1 {
  background-color: none;
  background-image: url(../images/pattern1.jpg);
  background-repeat: repeat;
  background-size: contain;
  width: 100%;
}








/* Mobile, landscape */
@media only screen and (min-width: 481px) {
  .services-title {
    font-size: 32px;
  }

  span.services-description p {
    font-size: 16px;
    padding: 10px;
  }

}

/* Tablet, portrait */
@media only screen and (min-width: 769px) {}

/* Tablet, landscape */
@media only screen and (min-width: 1025px) {}

/* Laptop displays */
@media only screen and (min-width: 1200px) {
  .container-div{
    width: 500px;
    margin:0;
    padding:0;
    /* display: block; */
  }


}
