* {
  box-sizing: border-box;
}

/* Clearfix used in order to eliminate the space between element where one contain the other.
clearfix with selector after is used to add something after its content.
content property is used with after selector and introduces the content which being contained between ""
display property determines the appearance behaviour of an element. 
->table value means it will behave like a table, more analytically it will not take more space left or right more than it should.
->clear property determines in which sides of an element another element cannot float */
.clearfix:after {
  content: "";
  display: table;
  clear: both;
}

