  font-family: "Montserrat", "Roboto", sans-serif;
  font-size: 1.5rem;
  background-color: white;
}

.flex-hair,
.flex-styl {
  display:flex;
  flex-direction:row;
  flex-wrap:wrap;
}

/* overflow property for the divs containing the images of each service */
.responsive {
  margin: auto;
  margin-top: 30px;
  width: 44%;
  overflow: hidden;
}

.responsive-treat {
  margin: auto;
  margin-top: 6%;
  width: 94%;
  padding: 0px;
  overflow: hidden;
}

.responsive-treat3 {
  margin: auto;
  margin-top: 6%;
  margin-bottom: 6%;
  width: 94%;
  padding: 0px;
  overflow: hidden;
}

/* image zoom during hover */
.responsive:hover img {
  cursor: pointer;
  transform: scale(1.1);
  -webkit-transform: scale(1.1);
  -moz-transform: scale(1.1);
  -ms-transform: scale(1.1);
  -o-transform: scale(1.1);
}

/* smooth transition for hover when image's zoom effect takes place */
.image-zoom {
  width: 100%;
  display: block;
  transition: -webkit-transform 0.5s ease;
  transition: transform 0.5s ease;
  transition: transform 0.5s ease, -webkit-transform 0.5s ease;
  -webkit-transition: transform 0.5s ease;
  -moz-transition: transform 0.5s ease;
  -ms-transition: transform 0.5s ease;
  -o-transition: transform 0.5s ease;
}

/* when user clicks image it becomes saturated */
.responsive:active img {
  filter: saturate(10);
  -webkit-filter: saturate(10);
}

/*Child του responsive, περιέχει το slider-carousel*/
.slider {
  margin: auto;
  margin-top: 30px;
  margin-bottom: 60px;
  width: 94%;
  height: auto;
  padding: 0px;
  overflow: hidden;
}

/*Child του slider. Περιέχει την κάθε εικόνα με το overlay text*/
.slider-img-container {
  position: relative;
  padding: 0;
  margin: 0;
  width: 100%;
}

ul.alice-carousel__dots {
  margin: 0px 3px 5px;
}

li.alice-carousel__dots-item:not(.__custom):hover,
li.alice-carousel__dots-item:not(.__custom).__active {
  background-color: red;
}

/*Child του slider-img-container. */
.slider-img {
  width: 100%;
  height: auto;
  padding: 0;
  margin: 0;
  object-fit: cover;
  display: block;
}

/*overlay text κατα το hover της εικόνας*/
.overlay {
  cursor: default;
  display: flex;
  /*Positionthebackgroundtext*/
  position: absolute;
  /*Atthebottom*/
  bottom: 0;
  /*Fallbackcolor*/
  background: rgb(0, 0, 0);
  /*Blackbackgroundwith0.5opacity*/
  background: rgba(0, 0, 0, 0.5);
  /*Greytext*/
  color: #f1f1f1;
  /*Fullwidth*/ /*width: 100%;
  */ /*Somepadding*/
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 10px;
  width: 100%;
  transition: opacity 0.2s ease;
  opacity: 0;
  transform: translateY(0%);
  -webkit-transition: opacity 0.2s ease;
  -moz-transition: opacity 0.2s ease;
  -ms-transition: opacity 0.2s ease;
  -o-transition: opacity 0.2s ease;
  -webkit-transform: translateY(0%);
  -moz-transform: translateY(0%);
  -ms-transform: translateY(0%);
  -o-transform: translateY(0%);
  font-family: "Montserrat", "Roboto", sans-serif;
  font-size: 0.5rem;
}

div.slider-img-container:hover div.overlay {
  opacity: 1;
}

div.slider-img-container:active img {
  filter: saturate(10);
  -webkit-filter: saturate(10);
}

