* {
    box-sizing: border-box;
}

.pattern {
    background-image: url(/hair_salon_project_under_development/static/media/pattern_contact.32f68990.jpg);
    background-repeat: repeat;
    background-size: contain;
    width: 100%;
}

.container-contact {
    display: flex;
    padding: 5vh 0;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
}

.container-contact>p {
    color: white;
    background-color: black;
    padding-left: 4px;
    padding-right: 4px;
    font-family: "Righteous", cursive;
}

p:nth-of-type(3),
p:nth-of-type(4) {
    padding-bottom: 0;
}

.pageTitle {
    color: white;
    font-family: "Righteous", cursive;
    font-weight: bold;
    font-size: 32px;
    letter-spacing: 1px;
}

.pageHeaders {
    margin: 30px 0 0;
    font-weight: bold;
    font-size: 16px;
}

p {
    font-size: 13px;
}

.fromFlex{
    display: flex;
    justify-content: center;
    width: 300px;
    background-color:red;
}

.form-group {
    width:80vw;
    padding-top: 0;
    margin-top: 0;
}

input[type="text"],
.miaklasi,
textarea {
    margin: 2px 0;
    width: 100%;
    background-color: rgba(255, 255, 255, 0.6);
    border: solid 2px transparent;
    font-family: "Montserrat", "Roboto", sans-serif;
    font-weight: normal;
    font-size: 12px;
    box-sizing: border-box;
}

input[type="text"]:hover,
.miaklasi:hover,
textarea:hover {
    background-color: rgba(255, 255, 255, 1);
    border: 2px solid #F24362;
    box-sizing: border-box;
}

.radioOptions {
    background-color: black;
    color: white;
    font-family: "Righteous", cursive;
    font-weight: normal;
    font-size: 13px;
}

input[type="radio"] {
    background-color: black;
}

.form-control-button {
    border-radius: 10px;
    margin-top: 5px;
    font-family: "Righteous", cursive;
    font-size: 13px;
}

.form-control-button:hover {
    background-color: #F24362;
    border: 2px solid white;
    font-weight: bold;
}

