/* Mobile landscape and portrait displays less than 568px width*/
.flex-footer {
  display: flex;
  flex-direction: row;
  background-color: none;
  color: black;
  font-family: "Righteous", cursive;
  font-size: 13px;
  padding: 0 3vw 3vw 3vw;
  margin: 0;
  height: auto;
  justify-content: space-between;
  align-items: flex-end;
}

div:nth-last-child(2) {
  z-index: 1;
}

h1 {
  padding: 0;
  margin: 0;
}

.div-logo-footer {
  padding: 10px;
}

.logowest-footer {
  width: 5rem;
  height: auto;
  margin: auto;
}

p {
  margin: 0;
  padding-top: 0;
  padding-bottom: 10px;
}

.copyright_notice {
  color: rgba(0, 0, 0, 0.6);
  padding: 0px;
}


/* Mobile, landscape */
@media only screen and (min-width: 481px) {
  .flex-footer p{
    font-size:16px;
  }
}

/* Tablet, portrait */
@media only screen and (min-width: 769px) {}

/* Tablet, landscape */
@media only screen and (min-width: 1025px) {}

/* Laptop displays */
@media only screen and (min-width: 1200px) {
  .flex-footer {
    padding: 0 3vw 3vw 3.5vw;
  }
}

.btnContainer {
   position: fixed;
   bottom: 3vw;
   right: 3%;
   height: autox;
   z-index: 1;
   cursor: pointer;
   border-style: none;
   width: 50px;
   padding: 2px 2px 2px 2px;
   border-style: groove;

}

.btnImage {
   background-size: contain;
}

@media only screen and (min-width: 1200px) {
   .btnContainer {
      right: 3.5%;
   }
}

